@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

// les polices
$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;

// les couleurs
$color-1: #2a1d52;
$color-2: #6C77F4;
$color-3: rgb(99, 164, 233);
$color-4: #4FEDD2;
$color-5: #36D0EA;
$white: #ddd;
$black: #333;

$borderRadius: 25px; // définir des coins arrondis pour la bordure d'un élément
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

// centrer verticalement
@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

// réglages de base à tous nos éléments
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 1rem auto;
    max-width: 1200px; // taille max du site
    font-family: $font-1;
    color: $white;
}

button {
    cursor: pointer;
}

li {
    list-style-type: none;
}

// liens hypertext
a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
}

// liens hypertext visités
a:visited {
    color: $black;
}

// réglages de base pour nos pages
.home,
.knowledges,
.portfolio,
.contact {
    display: flex;

    @media screen and (max-width: 850px) {
        display: block !important;
    }
}

.homeContent,
.knowledgesContent,
.portfolioContent,
.contactContent {
    margin-left: .4rem;

    @media screen and (max-width: 850px) {
        margin-left: 0 !important;
    }
}